


  .top-container {
    display: flex;
    justify-content: space-between;
    background-color: rgb(255, 255, 255);
    border-bottom-color: yellow;
    border-bottom-width: 5px;
    border-color: yellow;
  }
  
  .top-container > div {
    margin: 30px;
    padding: 10px;
  }
  
  .nameline {
    font-size: 20px;
  }
  
  .careerline {
    font-size: 18px;
  }
  
  .detailline {
    font-size: 10px;
  }
  
  .top-page-wrapper {
    margin: 10px;
  }
  
  .resume-wrapper {
    margin: 15px;
  }
  
  .skill-container {
    display: flex;
    justify-content: flex-start;
  
  }
  
  .skill-lister {
    display: flex;
    flex-direction: column;
    flex-basis: 100%;
    flex: 1;
  }
  
  .row {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
  }
  
  .row-gx {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
  }
  
  .double-column {
